import React from 'react';
import {Root, Header, Spinner, CardGrid, Card, Button, ModalPage, ModalRoot, ModalPageHeader, PanelHeaderButton, Group, Separator, Placeholder} from "@vkontakte/vkui";
import Icon20CalendarOutline from '@vkontakte/icons/dist/20/calendar_outline';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon16CancelCircleOutline from '@vkontakte/icons/dist/16/cancel_circle_outline';
import Icon16Recent from '@vkontakte/icons/dist/16/recent';
import Icon16CheckCircleOutline from '@vkontakte/icons/dist/16/check_circle_outline';
import Icon16ReplyOutline from '@vkontakte/icons/dist/16/reply_outline';
import Icon16InfoOutline from '@vkontakte/icons/dist/16/info_outline';
import Time from "../Time";

var tournamentListCache
var tournamentInfoCache
var isLoad = false
           
class Finish extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = {
            activeModal: null,
            modalHistory: [],
            content: <Spinner/>,
        };
    
        this.modalBack = () => {
          this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);
        };
    }

    setActiveModal(activeModal) {
        activeModal = activeModal || null;
        let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];
    
        if (activeModal === null) {
          modalHistory = [];
        } else if (modalHistory.indexOf(activeModal) !== -1) {
          modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
        } else {
          modalHistory.push(activeModal);
        }
    
        this.setState({
          activeModal,
          modalHistory
        });
    };

    getRender() {
        if (tournamentListCache.data.length !== 0) {
            const Modal = (
                <ModalRoot activeModal={this.state.activeModal} onClose={this.modalBack}>
                    {
                        tournamentListCache['data'].map((elem) =>
                            <ModalPage id={elem.title}
                                onClose={this.modalBack}
                                header={
                                    <ModalPageHeader left={<PanelHeaderButton onClick={this.modalBack}><Icon24Cancel/></PanelHeaderButton>}>
                                        Информация 
                                    </ModalPageHeader>
                                }
                            >
                                <div className="ModalPage__Content Turnament-Information__Group">
                                    <Group>
                                        <h4>{elem.title}</h4>
                                    </Group>
                                    <Separator className="CastomSeparator" style={{ margin: '12px 0' }} />
                                    <Group>
                                        <div className="TournamentCard__Info-Main-Title">Регистрация</div>
                                        <div style={{marginLeft: 5}}>
                                            <span style={{display: "inline-flex", alignItems: "center"}}>
                                                <Icon16CheckCircleOutline width={17} height={17}/><Time timestamp={elem.registration_start_at}/>
                                            </span>
                                            <br/>
                                            <span style={{display: "inline-flex", alignItems: "center"}}>
                                                <Icon16CancelCircleOutline width={17} height={17}/><Time timestamp={elem.registration_end_at}/>
                                            </span>
                                        </div>
                                        <br/>

                                        <div className="TournamentCard__Info-Main-Title">Продолжительность боёв</div>
                                        <div style={{marginLeft: 5}}>
                                            <span style={{display: "inline-flex", alignItems: "center"}}>
                                                <Icon16Recent width={17} height={17} style={{color: "green"}}/><Time timestamp={elem.start_at}/>
                                            </span>
                                            <br/>
                                            <span style={{display: "inline-flex", alignItems: "center"}}>
                                                <Icon16Recent width={17} height={17} style={{color: "rgb(200, 0, 0)"}}/><Time timestamp={elem.end_at}/>
                                            </span>
                                        </div>
                                        <br/>

                                        <div className="TournamentCard__Info-Main-Title">Команды</div>
                                        <div style={{marginLeft: 5}}>
                                            <span style={{display: "inline-flex", alignItems: "center"}}>
                                                <span style={{fontWeight: "450"}}>Максимальное число команд:&nbsp;</span> {tournamentInfoCache[elem["tournament_id"]].max}
                                            </span>
                                            <br/>
                                            <span style={{display: "inline-flex", alignItems: "center"}}>
                                                <span style={{fontWeight: "450"}}>Приняло участие:&nbsp;</span> {tournamentInfoCache[elem["tournament_id"]].total}
                                            </span>
                                        </div>
                                    </Group>
                                    <Separator className="CastomSeparator" style={{ margin: '12px 0' }} />
                                    <Group>
                                        <p className="Pre">
                                            {elem.description}
                                        </p>
                                    </Group>
                                </div>
                            </ModalPage>    
                        )
                    }
                </ModalRoot>
            );
            
            var List = (
                <Root modal={Modal}>
                {
                    tournamentListCache['data'].map((elem) => 
                    <CardGrid>
                        <Card size="l" mode="outline">
                                <div className={elem.status + " border"}></div>
                                <div className="TournamentCard">
                                    <div className="TournamentCard__Header">{elem.title}</div>
                                    <div className="TornamentCard__Info">
                                        <div className="TornamentCard__Info-Start">
                                            <Icon20CalendarOutline width={25} height={25}/>
                                            <div className="TornamentCard__Info-Start-Text">
                                                <Time timestamp={elem.start_at} mode="day"/>
                                            </div>
                                        </div>
                                        <div className="TournamentCard__Info-Main">
                                            Турнир завершён
                                        </div>
                                    </div>
                                    <div className="TornamentCard__Footer">
                                        <Button className="TornamentCard__Footer-Button" mode="outline" before={<Icon16InfoOutline/>} onClick={() => this.setActiveModal(elem.title)}>Подробнее</Button> 
                                        <Button className="TornamentCard__Footer-Button TornamentCard__Footer-SmallButton" mode="outline"><Icon16ReplyOutline/></Button>
                                    </div> 
                                </div>
                        </Card>
                    </CardGrid>
                    )
                }
            </Root>
            )
        } else {
           var List = ( <Placeholder>Нет завершенных турниров</Placeholder>);
        }
        return(List)
    }

    render() {
        if (!isLoad) {
            var p = new Promise((resolve) => {
                var p1 = new Promise((resolve) => {
                    var tournament = new XMLHttpRequest();
                    tournament.open("POST", "https://api.wotblitz.ru/wotb/tournaments/list/?application_id=132530213b8f23d4c0e1d1f423c307a9&status=finished", true)
                    tournament.onload = function () {
                        resolve(this.response)
                    }
                    tournament.send()
                })
    
                p1.then(c => {
                    var p3 = new Promise((resolve) => {
                        tournamentListCache = JSON.parse(c)
                        var b = tournamentListCache['data'].reduce(
                            function (p, e) {
                                var tournamentInfoRequest = new XMLHttpRequest();
                                tournamentInfoRequest.open("POST", "https://api.wotblitz.ru/wotb/tournaments/info/?application_id=132530213b8f23d4c0e1d1f423c307a9&tournament_id=" + e.tournament_id + "&fields=teams", false)
                                tournamentInfoRequest.send()
                                p[e["tournament_id"]] = JSON.parse(tournamentInfoRequest.responseText).data[e["tournament_id"]].teams 
                                return p
                        }, {})
                        resolve(b) 
                    })
                    p3.then(b => {
                        tournamentInfoCache = b

                        resolve()
                    })
                })
            })

            p.then(() => {
                isLoad = true
                this.setState({content: this.getRender()})
            })
        } else {
            return(this.getRender())
        }
        return(this.state.content); 
    }
}

export default Finish;